import React from "react";
import { graphql } from "gatsby";
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from "gatsby-source-contentful/rich-text";
import { FluidObject } from "gatsby-image";
import "./index.scss";
import { Layout } from "../components/Layout";
import { BackgroundBlock, ImageBlock, ServiceBlock } from "../components";
import "../styles/reset.css";
import "../styles/styles.scss";

type HomeProps = {
  data: {
    allContentfulFeaturePage: {
      nodes: {
        button: string;
        description: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        heading1: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        heading2: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        image1: { fluid: FluidObject };
        image2: { fluid: FluidObject };
      }[];
    };
    allContentfulFeaturePageExpectations: {
      nodes: {
        title: string;
        description: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        image: { fluid: FluidObject };
      }[];
    };
  };
};
const IndexPage = ({ data }: HomeProps) => {
  const expectations = {
    heading: "What can you expect from us",
  };
  const Features = data.allContentfulFeaturePage.nodes;
  const Services = data.allContentfulFeaturePageExpectations.nodes;
  console.log(data);

  return (
    <Layout>
      <BackgroundBlock
        backgroundImage="radial-gradient(circle at top left, #ffffff 0%, #eaf1f9 63%, #d9e2ed 96%)"
        height="100vh"
        width="100%"
      />
      <h1> &nbsp; Feature Page</h1>
      {Features.map((feature,index) => {
        const temp = {
          title: feature.heading1,
          image: feature.image1,
          description: feature.description,
          subHeading: {
            title: feature.heading2,
            image: feature.image2,
          }
        }
        return (<><ImageBlock key={index} align="right" data={temp} /><div className="spacer"></div></>);
      })}

      {/* <ImageBlock data={Features[1]} />
      <div className="spacer"></div>
      <ImageBlock align="right" data={Features[2]} />
      <div className="spacer"></div> */}
      <ServiceBlock heading={expectations.heading} services={Services} />
    </Layout>
  );
};

export default IndexPage;
export const query = graphql`
  query {
    allContentfulFeaturePage(
      filter: { node_locale: { eq: "en-US" } }
      sort: { fields: sort, order: ASC }
    ) {
      nodes {
        button
        description {
          raw
        }
        heading1 {
          raw
        }
        heading2 {
          raw
        }
        image2 {
          file {
            url
          }
        }
        image1 {
          fluid(maxWidth: 980) {
            ...GatsbyContentfulFluid
          }
        }
      }
    }
    allContentfulFeaturePageExpectations(
      filter: { node_locale: { eq: "en-US" } }
      sort: { fields: sort, order: ASC }
    ) {
      nodes {
        title
        description {
          raw
        }
        image {
          fluid(maxWidth: 980) {
            ...GatsbyContentfulFluid
          }
        }
      }
    }
  }
`;
